<!--  -->
<template lang="html">
  <div class="page-register">
    <article class="header">
      <header>
        <img src='@/assets/images/logo2.png'>
        <span class="login">
          <em class="bold">?</em>
          <a href="/login">
            <el-button 
              type="primary" 
              size="small"></el-button>
          </a>
        </span>
      </header>
    </article>
    <section>
      <el-form 
        ref="ruleForm" 
        :model="ruleForm" 
        :rules="rules" 
        label-width="100px" 
        class="demo-ruleForm">
        <el-form-item 
          label="" 
          prop="name">
          <el-input v-model="ruleForm.name"/>
        </el-form-item>
        <el-form-item 
          label="" 
          prop="email">
          <el-input v-model="ruleForm.email"/>
          <el-button 
            size="mini" 
            round 
            @click="sendMsg"></el-button>
          <span class="status">{{ statusMsg }}</span>
        </el-form-item>
        <el-form-item 
          label="" 
          prop="code">
          <el-input 
            v-model="ruleForm.code" 
            maxlength="6"/>
        </el-form-item>
        <el-form-item 
          label="" 
          prop="pwd">
          <el-input 
            v-model="ruleForm.pwd" 
            type="password"/>
        </el-form-item>
        <el-form-item 
          label="" 
          prop="cpwd">
          <el-input 
            v-model="ruleForm.cpwd" 
            type="password"/>
        </el-form-item>
        <el-form-item>
          <el-button 
            type="primary" 
            @click="register"></el-button>
          <div class="error">{{ error }}</div>
        </el-form-item>
        <el-form-item>
          <a 
            href="https://www.meituan.com/about/terms" 
            target="_blank" 
            class="f1">
          </a>
        </el-form-item>
      </el-form>
    </section>
  </div>
</template>

<script>
// 
import CryptoJS from 'crypto-js'
export default {
    layout:'blank',
    data() {
        return {
          statusMsg:'',
          error:'',
          ruleForm:{
            name:'',
            code:'',
            pwd:'',
            cpwd:'',
            email:''
          },
          rules:{
            name:[{
              required:true,type:'string',message:'',trigger:'blur'
            }],
            email:[{
              required:true,
              type:'email',
              message:'',
              trigger:'blur'
            }],
            pwd:[{
              required:true,
              message:'',
              trigger:'blur'
            }],
            cpwd:[{
              required:true,
              message:'',
              trigger:'blur'
            },{
              validator:(rule,value,callback)=>{
                if(value===''){
                  callback(new Error(''))
                }else if(value!==this.ruleForm.pwd){
                  callback(new Error(''))
                }else{
                  // callback
                  callback()
                }
              },
              trigger:'blur'
            }]
          }
        }
    },
    methods:{
      sendMsg:function(){
          const self=this;
          let namePass
          let emailPass
          if(self.timerid){
            return false
          }
          // 
          this.$refs['ruleForm'].validateField('name',valid=>{
            namePass=valid
          })
          // 
          self.statusMsg=''
          if(namePass){
            return false
          }
          this.$refs['ruleForm'].validateField('email',valid=>{
            emailPass=valid
          })
          // 
          if(!namePass&&!emailPass){
              self.$http.post('code',{
                // 
                // username:encodeURIComponent(self.ruleForm.name),
                receiver:self.ruleForm.email
              }).then(({
                status,data
              })=>{
                if(status==200&&data&&data.code===0){
                  let count=60;
                  self.statusMsg=`,${count--}`
                  self.timerid=setInterval(function(){
                    self.statusMsg=`,${count--}`
                    if(count===0){
                      clearInterval(self.timerid)
                     self.timerid=null
                    }
                  },1000)
                }else{
                  self.statusMsg=data.msg
                }
              })
          }
      },
      register:function(){
        let self=this;
        this.$refs['ruleForm'].validate((valid)=>{
            console.log(valid)
          if(valid){
            self.$http.post('add_user',{
              username:window.encodeURIComponent(self.ruleForm.name),
              password:CryptoJS.MD5(self.ruleForm.pwd).toString(),
              email:self.ruleForm.email,
              code:self.ruleForm.code
            }).then(({status,data})=>{
              if(status===200){
                
                if(data&&data.code===0){
                  location.href='/login'
                }else{
                  self.error=data.msg
                }
              }else{
                self.error=`,:${status}`
              }
              setTimeout(() => {
                self.error=''
              }, 1500);
            })
          }else{
            console.log('')
          }
        })
      }
    }
}

</script>
<style lang='scss' >
a{
  text-decoration:none;
}
@import '@/assets/css/register/index.scss';
</style>